import React, { useEffect, useRef } from "react";
import { ReactComponent as FoodBag } from "../../assets/foodBag.svg";
import Navigation from "../Navigation/Navigation";
import Search from "../Search/Search";
import gsap from "gsap";

const Home = () => {
  const imageWrapper = useRef(null);
  let homeWrapper = useRef(null);
  let homeHeader = useRef(null);

  useEffect(() => {
    const [elements] = imageWrapper.current.children;

    const bag = elements.querySelector('#bag');
    const bananas = elements.querySelector('#bananas');
    const cabbage = elements.querySelector('#cabbage');
    const apple = elements.querySelector('#apple');
    const redApple = elements.querySelector('#redApple');
    const bottle = elements.querySelector('#bottle');
    const bagShape = elements.querySelector('#bagShape');
    const cheese = elements.querySelector('#cheese');
    const wine = elements.querySelector('#wine');
    const green = elements.querySelector('#green');
    const jam = elements.querySelector('#jam');
    const bagiette = elements.querySelector('#bagiette');
    const radish = elements.querySelector('#radish');
    const appleShade = elements.querySelector('#apple_shade');
    const bagShade = elements.querySelector('#bag_shade');
    const cheeseShade = elements.querySelector('#cheese_shade');
    const cabbageShade = elements.querySelector('#cabbage_shade');
    const jamShade = elements.querySelector('#jam_shade');
    
    
    gsap.set([bag, bananas, cabbage, apple, redApple, bottle, cheese, wine, bagShape , green, jam, radish, bagiette, appleShade, cabbageShade, cheeseShade, bagShade,jamShade, homeHeader], {autoAlpha: 0});

    const tl = gsap.timeline({ defaults: { ease: "power3.inOut" } });

    tl.to(homeWrapper, {duration: 0, css: {visibility: 'visible'}})
      .fromTo(bag, {y: '-=100'}, {duration: 0.6, y: '+=100', autoAlpha: 1})
      .to(bagShade, {duration: 0.2, autoAlpha: 1})
      .fromTo(bananas, {y: '-=100'}, {duration: 0.6, y: '+=100', autoAlpha: 1}, "-=0.6")
      .fromTo(radish, {y: '-=100'}, {duration: 0.6, y: '+=100', autoAlpha: 1}, "-=0.4")
      .fromTo(green, {y: '-=100'}, {duration: 0.6, y: '+=100', autoAlpha: 1}, "-=0.4")
      .fromTo(redApple, {y: '-=100'}, {duration: 0.6, y: '+=100', autoAlpha: 1}, "-=0.4")
      .fromTo(bagiette, {y: '-=100'}, {duration: 0.6, y: '+=100', autoAlpha: 1}, "-=0.4")
      .fromTo(bottle, {x: '-=100'}, {duration: 0.6, x: '+=100', autoAlpha: 1}, "-=0.5")
      .fromTo(cabbage, {x: '+=100'}, {duration: 0.6, x: '-=100', autoAlpha: 1}, "-=0.5")
      .to(cabbageShade, {duration: 0.2, autoAlpha: 1}, "-=0.2")
      .fromTo(apple, {x: '-=100'}, {duration: 0.6, x: '+=100', autoAlpha: 1}, "-=0.5")
      .to(appleShade, {duration: 0.2, autoAlpha: 1}, "-=0.2")
      .fromTo(wine, {x: '+=100'}, {duration: 0.6, x: '-=100', autoAlpha: 1},"-=0.5")
      .fromTo(jam, {x: '-=100'}, {duration: 0.6, x: '+=100', autoAlpha: 1}, "-=0.5")
      .to(jamShade, {duration: 0.2, autoAlpha: 1}, "-=0.2")
      .fromTo(cheese, {x: '+=100'}, {duration: 0.6, x: '-=100', autoAlpha: 1}, "-=0.5")
      .to(cheeseShade, {duration: 0.2, autoAlpha: 1}, "-=0.2")
      .fromTo(homeHeader, {y: "-=20"} ,{duration: 1, y: "+=20", autoAlpha: 1});
  }, [])
  
  return (
    <>
      <div className="Home" ref={el => homeWrapper = el}>
        <div className='Home__imageWrpper' ref={imageWrapper}>
        <FoodBag className="Home__foodBag" />
        </div>
        <h1 className="Home__header" ref={el => homeHeader = el}>Witaj na Food Site!</h1>
      </div>
      <Navigation />
      <Search />
    </>
  );
};

export default Home;
