import React, {useEffect, useRef} from "react";
import Navigation from '../Navigation/Navigation';
import Search from '../Search/Search';
import gsap from 'gsap';


const FoodBook = () => {
  let componentWrapper = useRef(null);
  
  useEffect(() => {
      gsap.set(componentWrapper, {autoAlpha: 0});

      const tl = gsap.timeline({ defaults: { ease: "power3.inOut" } });
      tl.to(componentWrapper, {duration: 0.5, autoAlpha: 1});
  })

  return (
    <>
      <div ref={el => componentWrapper = el} className="FoodBook">
        <h1 className="FoodBook__header">Food Book</h1>
        <div className="FoodBook__foodCategoriesBox">
          <div className="FoodBook__foodCategory FoodBook__foodCategory--breakfast"></div>
          <div className="FoodBook__foodCategory FoodBook__foodCategory--dinner"></div>
          <div className="FoodBook__foodCategory FoodBook__foodCategory--supper"></div>
          <div className="FoodBook__foodCategory FoodBook__foodCategory--snacks"></div>
        </div>
      </div>
      <Navigation />
      <Search />
    </>
  );
};

export default FoodBook;
